@import url("https://fonts.googleapis.com/css?family=Open+Sans");

body {
  background-color: #f5f5f5;
  font-family: "Open Sans", sans-serif;
}

h2 {
  font-size: 24px;
  font-weight: 700;
}

.body {
  margin: 0 120px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.IV-back-button {
  color: #c3272e;
  margin: 0 auto 0 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 600;
  margin-top: 30px;
  cursor: pointer;
  background: none;
  border: none;
}

.IV-back-button img {
  margin-right: 5px;
}

.download-button {
  color: #7f1922;
  font-weight: 600;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: auto;
}

.tableauVisualization {
  height: auto;
  max-width: 100%;
}

.IV-description {
  text-align: left;
  font-size: 20px;
  margin-bottom: 60px;
}

.IV-Top-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}

p span {
  display: block;
  margin: 15px 0px;
  text-align: left;
}

#Analysis {
  text-align: left;
  margin-top: 60px;
}
